#player-bar {
  background: hsla(0, 0%, 0%, 0.1);
  bottom: 0;
  height: 100px;
  position: fixed;
  width: 100%;
}

#buttons {
  width: 300px;
  margin: auto;
  text-align: center;
}

#buttons button {
  border: none;
  background: none;
  color: black;
  font-size: 1.2rem;
  color: rgba(0, 0, 0, 0.5);
  /* color: hsla(0, 0%, 100%, 0.5); */
  -webkit-transition: all 0.2s ease;
  -o-transition: all 0.2s ease;
  transition: all 0.2s ease;
  cursor: pointer;
}

#buttons button:hover, #buttons button:focus {
  outline: none;
  /* color: rgba(0, 0, 0, 1); */
  color: hsla(0, 0%, 100%, 1);
  text-shadow: 2px 4px 3px hsla(0,0%,0%,0.25);
}

#buttons #play-pause {
  color: rgba(0, 0, 0, 0.75);
  /* color: hsla(0, 0%, 100%, 0.8); */
  font-size: 2rem;
  margin: 0.5rem 1rem 0;
  -webkit-transform: translateY(0.3rem);
      -ms-transform: translateY(0.3rem);
          transform: translateY(0.3rem);
}

#buttons #play-pause:hover {
  /* color: rgba(0, 0, 0, 1); */
  color: hsla(0, 0%, 100%, 1);
}

/* #play-pause .ion-pause {
  display: block;
}

#play-pause .ion-play {
  display: block;
} */

#time-control input {
  float: left;
  width: calc(100% - 80px);
}

#time-control {
  margin: 0.5rem auto;
  /* max-width: 640px; */
  width: 50%;
}

#time-control div,
#volume-control div {
  font-size: 0.8rem;
  color: rgba(0, 0, 0, 1);
  /* color: hsla(0, 0%, 100%, 0.4); */
  width: 35px;
  display: block;
  float: left;
  text-align: center;
}

#volume-control div {
  font-size: 0.9rem;
  width: 20px;
}

#volume-control {
  /* transform: translateY(-50%); */
  /* top: 66%; */
  position: absolute;
  right: 2rem;
  width: 15%;
}

#volume-control input {
  float: left;
  width: calc(100% - 40px);
}

input[type=range] {
  -webkit-appearance: none;
  width: 100%;
  margin: 5px 0;
}
input[type=range]:focus {
  outline: none;
}
input[type=range]::-webkit-slider-runnable-track {
  width: 100%;
  height: 5px;
  cursor: pointer;
  -webkit-box-shadow: 1.5px 1.5px 1px #000000, 0px 0px 1.5px #0d0d0d;
          box-shadow: 1.5px 1.5px 1px #000000, 0px 0px 1.5px #0d0d0d;
  background: #969696;
  border-radius: 1px;
  border: 1px solid #c8c8c8;
}
input[type=range]::-webkit-slider-thumb {
  -webkit-box-shadow: 1px 1px 1px #000000, 0px 0px 1px #0d0d0d;
          box-shadow: 1px 1px 1px #000000, 0px 0px 1px #0d0d0d;
  border: 1px solid #000000;
  height: 15px;
  width: 1px;
  border-radius: 4px;
  background: #646464;
  cursor: pointer;
  -webkit-appearance: none;
  margin-top: -6px;
}
input[type=range]:focus::-webkit-slider-runnable-track {
  background: #ffffff;
}
input[type=range]::-moz-range-track {
  width: 100%;
  height: 5px;
  cursor: pointer;
  box-shadow: 1.5px 1.5px 1px #000000, 0px 0px 1.5px #0d0d0d;
  background: #969696;
  border-radius: 1px;
  border: 1px solid #c8c8c8;
}
input[type=range]::-moz-range-thumb {
  box-shadow: 1px 1px 1px #000000, 0px 0px 1px #0d0d0d;
  border: 1px solid #000000;
  height: 15px;
  width: 1px;
  border-radius: 4px;
  background: #646464;
  cursor: pointer;
}
input[type=range]::-ms-track {
  width: 100%;
  height: 5px;
  cursor: pointer;
  background: transparent;
  border-color: transparent;
  color: transparent;
}
input[type=range]::-ms-fill-lower {
  background: #171717;
  border: 1px solid #c8c8c8;
  border-radius: 2px;
  box-shadow: 1.5px 1.5px 1px #000000, 0px 0px 1.5px #0d0d0d;
}
input[type=range]::-ms-fill-upper {
  background: #969696;
  border: 1px solid #c8c8c8;
  border-radius: 2px;
  box-shadow: 1.5px 1.5px 1px #000000, 0px 0px 1.5px #0d0d0d;
}
input[type=range]::-ms-thumb {
  box-shadow: 1px 1px 1px #000000, 0px 0px 1px #0d0d0d;
  border: 1px solid #000000;
  height: 15px;
  width: 1px;
  border-radius: 4px;
  background: #646464;
  cursor: pointer;
  height: 5px;
}
input[type=range]:focus::-ms-fill-lower {
  background: #969696;
}
input[type=range]:focus::-ms-fill-upper {
  background: #ffffff;
}

